// this.data = {title:,...}
class TeacherHomeworkCorrect extends React.Component {
    constructor(props) {
        super(props);
        this.userType = this.props.userType;
        this.state = {
            answerId: -1,
            commitTime: "",
            content: "",
            note: "",
            checkId: -1,
            checkTime: "",
            score: 0,
            description: "",
            comment: ""
        };
    }

    componentDidMount() {
        let answerId, checkId;
        if (this.userType === 1) {  // 
            answerId = this.props.userData.answerId;
            checkId = this.props.userData.checkId;
        }
        else if (this.userType === 2) { // 
            answerId = this.props.ansCheckData.answerId;
            checkId = this.props.ansCheckData.checkId;
        }
        if (answerId === -1) {
            message.info("");
            return;
        }
        // get stu's answer
        {
            const args = {answerId: answerId};
            const callback = (data) => {
                if (data.status === 200) {
                    this.setState({
                        answerId: data.data.answerId,
                        commitTime: data.data.commitTime,
                        content: data.data.content,
                        note: data.data.note
                    });
                    message.success(data.msg);
                }
                else {
                    message.error(data.msg);
                }
            };
            getStuAnswerByAnsId(args, callback);
        }
        // get stu's check info
        {
            if (checkId !== -1) {
                const args = {checkId: checkId};
                const callback = (data) => {
                    if (data.status === 200) {
                        this.setState({
                            checkId: data.data.checkId,
                            checkTime: data.data.checkTime,
                            score: data.data.score,
                            description: data.data.description,
                            comment: data.data.comment
                        });
                        message.success(data.msg);
                    }
                    else {
                        message.error(data.msg);
                    }
                };
                getStuCheckByCheckId(args, callback);
            }
        }
    }

    handleSubmit = () => {

    };

    handlePublish = () => {

    };

    render() {
        const hwDoneData = {
            answerId: this.state.answerId,
            commitTime: this.state.commitTime,
            content: this.state.content,
            note: this.state.note,
            description: this.state.description
        };
        const hwCorrectData = {
            checkId: this.state.checkId,
            checkTime: this.state.checkTime,
            score: this.state.score,
            comment: this.state.comment
        };
        const userType = this.props.userType;
        return (
            <div className="homwork-correct-container">
                <Divider orientation="left"><p className="divider-content"></p></Divider>
                <HomeworkDetail data={this.props.homeworkData}/>
                <Divider orientation="left"><p className="divider-content"></p></Divider>
                <HomeworkDone hwDoneData={hwDoneData}
                              userData={this.props.userData}
                              userType={userType}
                              stdAns={false}
                />
                <Divider orientation="left"><p className="divider-content"></p></Divider>
                <HomeworkCorrect hwCorrectData={hwCorrectData}
                                 userType={userType}
                />
                {
                    (this.userType === 1) ? (
                        <div style={{marginTop:20}}>
                            <Button type="primary" onclick={this.handleSubmit}></Button>
                            <Button onclick={this.handlePublish} style={{marginLeft:20}}></Button>
                        </div>
                    ) : (
                        <></>
                    )
                }

            </div>
        );
    }
}

export default TeacherHomeworkCorrect;
